.content {
  height: 100vh;
  width: 100vw;
  background-color: #161b22;
}
.contener-information {
  width: 12%;
  .photo-main {
    display: block;

    // position: absolute;
    margin-left: 5vw;
    margin-top: -60px;
    .photo-me {
      border-radius: 200px;
      border: 2px solid rgb(104, 98, 98);
      width: 20vw;
      min-width: 100px;
    }

    width: 20vw;
    .information {
      h1 {
        display: flex;
        // width: 30vw;
      }
      p {
        a {
          display: inline-block;
          width: 20vw;
        }
      }
    }
  }
}

.content {
  display: flex;
}

.img-link {
  margin-left: 5px;
}

.email-img {
  min-width: 25vh;
  border-radius: 200px;
  border: 2px solid rgb(104, 98, 98);
}

.information {
  width: 22vw;
  color: rgb(161, 160, 160);
  p {
    margin-top: 20px;
    display: block;
    width: 100vh;
    a {
      text-decoration: none;
      color: rgb(64, 110, 238);
    }
    a:hover {
      color: rgb(26, 104, 192);
      transition: 0.3s;
    }
  }
}

.readme {
  display: block;
  margin: 20px 39vh 0px 210px;
  padding: 20px;
  height: min-content;
  min-width: 50%;
  width: auto; //width: min-content dla mobilki powinno dobrze wygladac
  border-radius: 8px;
  border: 1px solid rgb(104, 98, 98);
  color: rgb(161, 160, 160);
  div {
    // padding-left: 5px;
    h1 {
    }
    .reactgif {
      padding: 10px;
      padding-bottom: 15px;
    }
    // margin-left: 5px;
    div {
      a {
        text-decoration: none;
        color: rgb(189, 163, 16);
      }
      p {
        display: block;
        margin-top: 20px;
      }
      .skills {
        p {
          img {
            margin-left: 0;
            padding-top: 5px;
            display: block;
          }
        }
      }
    }
  }
}
